import "./styles.css";
import { useState, useEffect } from "react";
import axios from "axios";

export default function App() {
  const [user, setUser] = useState("");
  useEffect(() => {
    //});
    axios
      .get("./Data.json")
      .then(function (response) {
        // handle success
        setUser(response.data);
        console.log(response.data);
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
        // always executed
      });
  }, []);
  console.log(user);

  return (
    <div>
      <h2> Doctors List</h2>
      {Array.from(user).map((val) => {
        return (
          <div>
            <ul>
              <ol>Catagory: {val.category}</ol>
              <ol>ID: {val.id}</ol>
              <ol>Name: {val.Name}</ol>
              <ol>Descripiton: {val.descripiton}</ol>
              <ol> {val.assure}</ol>
              <ol>Expreinece: {val.expreinece}</ol>
              <ol>Fee: {val.fee}</ol>
            </ul>
            ;
          </div>
        );
      })}
    </div>
  );
}
